<template>
</template>

<script>
  import * as firebase from 'firebase'

  export default {
    name: 'profile',

    created() {
      firebase
        .auth()
        .applyActionCode(this.$route.query.oobCode)
        .then(res => {
          console.log(res)
          return
          this.$router.push('profile')
        })
        .catch(console.log)
    }
  }
</script>



